#bodymovin {
  width: 600px;
  padding-right: 50px;
  margin: auto;
  margin-top: 200px;
  margin-bottom: 100px;
}
.frame {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  font-size: 0.75rem;
  z-index: 5;
}
.frame .corner {
  position: absolute;
  width: 40px;
  height: 40px;
  opacity: 0.5;
}
.frame .corner.top-left {
  top: 2.5%;
  left: 2%;
  border-top: solid 3px #fff;
  border-left: solid 3px #fff;
}
.frame .corner.top-right {
  top: 2.5%;
  right: 2%;
  border-top: solid 3px #fff;
  border-right: solid 3px #fff;
}
.frame .corner.bottom-right {
  bottom: 2.5%;
  right: 2%;
  border-bottom: solid 3px #fff;
  border-right: solid 3px #fff;
}
.frame .corner.bottom-left {
  bottom: 2.5%;
  left: 2%;
  border-bottom: solid 3px #fff;
  border-left: solid 3px #fff;
}
.frame .frame-logo {
  position: absolute;
  top: -12.5%;
  left: 125%;
  height: 1em;
  width: auto;
  opacity: 0.75;
}
.frame .frame-infos {
  opacity: 0.75;
}
.frame .frame-infos p {
  text-transform: uppercase;
  position: absolute;
  font-weight: 300;
}
.frame .frame-infos p span.green {
  color: #4f2;
  margin-right: 10px;
}
.frame .frame-infos p span.red {
  color: #f24;
  margin-right: 10px;
}
.frame .frame-infos .top-left {
  top: 2.5%;
  left: 15em;
}
.frame .frame-infos .top-right {
  top: 2.5%;
  right: calc(2.5% + 40px);
}
.frame .frame-infos .bottom {
  bottom: 2.5%;
  left: calc(2.5% + 40px);
}
.frame .frame-infos .bottom .pos-a,
.frame .frame-infos .bottom .pos-o {
  color: #3ac;
}
.frame .title {
  position: absolute;
  height: 8rem;
  top: 15%;
  left: 15%;
  color: #fff;
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.125s ease-out, transform 0.5s ease-out;
  will-change: transform, opacity;
}
.frame .title.hidden {
  opacity: 0;
  transform: translateY(50%);
}
.frame .title .stick {
  position: absolute;
  top: 0;
  left: -2.5rem;
  width: 1rem;
  height: 7.5rem;
  transform: skewX(-20deg);
  background: #913331;
}
.frame .title .title-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  font-size: 2.5rem;
  font-weight: 300;
}
.frame .title .title-bottom {
  position: absolute;
  bottom: 0;
  left: 0%;
  width: 100vw;
  font-size: 5rem;
  font-weight: 300;
}
.frame .title .title-bottom span {
  font-weight: 500;
}
.frame .chapter {
  position: absolute;
  left: 10%;
  top: calc(15% + 10em);
  width: 100vw;
}
.frame .chapter .title-chapter {
  position: absolute;
  left: 0;
  bottom: 5rem;
  font-size: 3.75rem;
  opacity: 0;
  will-change: opacity;
}
.frame .chapter .title-chapter::before {
  content: '/ ';
  color: #3ac;
  font-weight: bolder;
}
.frame .chapter .text-chapter {
  position: absolute;
  left: 3.2rem;
  bottom: 0rem;
  font-size: 1rem;
  width: 37.5vw;
  text-align: justify;
  line-height: 1.4;
}
.slide {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  transform: translate(100%, 0%);
  transition: opacity ease-in-out, transform ease-in-out;
  transition-delay: 0.75s;
  transition-duration: 0.75s;
  will-change: opacity, transform;
  z-index: 0;
}
.slide.active {
  opacity: 1;
  transform: translate(0, 0);
  transition-delay: 0s !important;
  z-index: 1;
}
.slide:first-child {
  left: 0;
}
.slide .bloc {
  position: relative;
  width: 100%;
  height: 100%;
}
.slide .bloc .section-info {
  position: absolute;
  bottom: 10rem;
  left: 12rem;
}
.slide .bloc .section-info p {
  color: #fff;
  padding-top: 10%;
  font-size: 1.25rem;
  font-weight: 300;
}
.slide .bloc .section-info span {
  font-size: 1.875rem;
  font-weight: 600;
}
.slide .bloc .section-info-numbers {
  position: absolute;
  bottom: 7rem;
  left: 12rem;
}
.slide .bloc .section-info-numbers p {
  color: #fff;
  padding-top: 10%;
  font-size: 1.25rem;
  font-weight: 300;
  margin-top: 1rem;
}
.slide .bloc .section-info-numbers span {
  display: inline;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 20rem;
  padding: 0.75rem;
  margin-left: 0.2rem;
}
.slide .bloc .section-info-numbers span.blue {
  border: solid 1.5px #36c;
}
.slide .bloc .section-info-numbers span.green {
  border: solid 1.5px #33cc57;
}
.slide .bloc .section-info-numbers span.violet {
  border: solid 1.5px #6133cc;
}
.slide .bloc .image {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: transform 0.25s ease-in-out;
  will-change: transform;
}
.slide .bloc.active .image {
  transform: scale(1.1875);
}
.slide:nth-child(1) .bloc:nth-child(1) .image {
