.circle {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  opacity: 1;
  transform-origin: 50% 50%;
  transform: scale(0);
  transition: opacity ease-in-out 0.25s, transform ease-in-out 0.25s;
  will-change: opacity, transform;
  z-index: 10;
}
.circle.shown {
  transform: scale(1);
}
.circle.shown:hover .stick-1 {
  transform: scaleY(1) skew(-20deg);
}
.circle.shown:hover .stick-1 .stick-2 {
  transform: scaleX(1) skew(20deg);
}
.circle.shown:hover .stick-1 .stick-2 .text {
  opacity: 1;
}
.circle::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0;
  transform-origin: 50% 50%;
  transition: opacity ease-in-out 0.25s, transform ease-in-out 0.25s;
  will-change: opacity, transform;
}
.circle::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: dashed #fff 2px;
  transform-origin: 50% 50%;
  animation: rotate-circle infinite 60s linear;
  will-change: transform;
}
.circle:hover::before {
  opacity: 0.125;
}
.circle:hover::after {
  animation-duration: 10s;
}
.circle .stick-1 {
  position: absolute;
  left: calc(50% - 1px);
  bottom: 100%;
  width: 2px;
  height: 30px;
  background-color: #fff;
  transform-origin: 50% 100%;
  transform: scaleY(0) skew(0);
  transition: transform 0.25s ease-in-out;
  will-change: transform;
}
.circle .stick-1 .stick-2 {
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 2px;
  background-color: #fff;
  transform-origin: 0 0;
  transform: scaleX(0) skew(20deg);
  transition: transform 0.25s ease-in-out;
  transition-delay: 0.25s;
  will-change: transform;
}
.circle .stick-1 .text {
  position: absolute;
  left: 125%;
  top: 0;
  width: 15rem;
  font-size: 0.625rem;
  font-weight: 200;
  text-align: justify;
  hyphens: auto;
}
.dots {
  position: absolute;
  left: 0;
  bottom: 7.5%;
  width: 100%;
  text-align: center;
  opacity: 0.75;
  z-index: 10;
}
.dots .dot {
  display: inline-block;
  position: relative;
  width: 1rem;
  height: 1rem;
  margin: 0.5rem;
  border: solid 0.125rem #fff;
  border-radius: 50%;
}
.dots .dot::after {
  content: '';
  position: absolute;
  left: 25%;
  top: 25%;
  width: 50%;
  height: 50%;
  background-color: #fff;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  will-change: opacity;
}
.dots .dot.active::after,
.dots .dot:hover::after {
  opacity: 1;
}
canvas {
  position: absolute;
  z-index: 2;
}
.parallax {
  will-change: transform;
}
